@import "../../utils/variables";
@import "../../utils/mixins";

@mixin halo($inset-color) {
    box-shadow: inset 0 10px #b5b8cd, 0 10px $inset-color, 0 0 0 40px hsla(0, 0%, 95%, 0.03),
        0 0 0 90px hsla(0, 0%, 95%, 0.02), 0 0 0 150px hsla(0, 0%, 95%, 0.015);
    margin: auto;
    @include mobile-only {
        box-shadow: inset 0 5px #b5b8cd, 0 5px $inset-color, 0 0 0 40px hsla(0, 0%, 95%, 0.03),
            0 0 0 90px hsla(0, 0%, 95%, 0.02), 0 0 0 150px hsla(0, 0%, 95%, 0.015);
    }
}

@mixin symbol {
    background-color: white;
    width: 200px;
    height: 200px;
    display: flex;
    border: inset 30px transparent;
    border-radius: 100%;
    background-origin: border-box;
    background-clip: content-box, border-box;
    @include mobile-only() {
        width: 100px;
        height: 100px;
        border: inset 15px transparent;
    }
}

.symbol-container {
    @include symbol();
    &--choice {
        @include symbol();
        width: 150px;
        height: 150px;
        border: inset 20px transparent;
        cursor: pointer;
        &__image {
            margin: auto;
            height: 80px;
            @include mobile-only() {
                height: 60px;
            }
        }
    }
    &--hidden {
        @include symbol();
        @include mobile-only() {
            width: 100px;
            height: 100px;
        }
        background-color: #1c2138;
    }
    &__image {
        margin: auto;
        height: 100px;
        @include mobile-only() {
            height: 60px;
        }
    }
}

.scissors {
    background-image: linear-gradient(white, white), radial-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
    box-shadow: inset 0 10px #b5b8cd, 0 10px #a36528;
    @include mobile-only {
        box-shadow: inset 0 5px #b5b8cd, 0 5px #a36528;
    }
    &--winner {
        @include halo(#a36528);
    }
}

